<template>
  <section class="main">
    <Header @show-addtask="toggleAddTask" title="Task Tracker" :shown="showAddTask"/>
    <AddTask :class="!showAddTask ? 'hidden' : ''" @add-task="addTask" />
    <Tasks @toggle-reminder="toggle" @delete-task="removeTask" :tasks="tasks" />
    <Footer />
  </section>
</template>

<script>
import Header from './components/Header'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'
import Footer from './components/Footer'
export default {
  name: "App",
  components: {
    Header,
    Tasks,
    AddTask,
    Footer
  },
  methods: {
    removeTask(id) {
      if(confirm("Are you sure you want to remove this task?")){
        this.tasks = this.tasks.filter((task) => {
          return task.id !== id;
        })
      }
    },
    toggle(id){
      this.tasks = this.tasks.map((task) => {
        if(task.id === id){
          task = {...task, reminder: !task.reminder}
          return task;
        }
        return task;
      })
    },
    addTask(task){
      this.tasks = [...this.tasks, task]
    },
    toggleAddTask(){
      this.showAddTask = !this.showAddTask;
    }
  },
  emits: ['delete-task', 'toggle-reminder', 'add-task'],
  data() {
    return {
      tasks: [],
      showAddTask: false,
    }
  },
  created() {
    this.tasks = [
      {
        id: 1,
        title: "Do grocery shopping",
        day: "May 14 at 10:00am",
        reminder: true,
        
      },
       {
        id: 2,
        title: "Complete client's project",
        day: "May 14 at 1:30pm",
        reminder: true,
        
      },
       {
        id: 3,
        title: "Sleep for 6 hours",
        day: "May 14 at 9:00pm",
        reminder: false,
        
      }
    ]
  },
};
</script>

