<template>
    <div @dblclick="$emit('toggle-reminder')" :class="[task.reminder ? 'reminder' : '', 'task']"  >
        <div class="task-head">
            <h4 class="title" >{{ task.title }}</h4>
            <i @click="onDelete" class="fa fa-times"></i>
        </div>
        <span>{{ task.day }}</span>
    </div>
</template>

<script>
    export default {
        name: 'Task',
        props: {
            task: Object
        },
        methods: {
            onDelete() {          
                this.$emit('delete-task');
            }
        }
    }
</script>

<style scoped>
    i{
        text-align: right;
        color: red;
        cursor: pointer;
    }
    .task{
        box-sizing: border-box;
        padding: 7px 10px 10px 10px;
        margin-top: 10px;
        text-align: left;
     
        
        box-shadow: 5px -5px 15px #2c3e5017;
        border: 1px solid #2c3e5017;
    }
    
    .title{
        font-weight: 400;
        font-size: 16px;
        margin: 0;
        text-align: left;
    }
    span {
        font-size: 14px;
        opacity: 0.8;
    }
    .task-head{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .reminder {
        border-left: 3px solid #2c3e50;
    }
</style>