<template>
    <form  @submit="createTask">
        <div class="form-control">
            <label for="task">Task Name</label>
            <input type="text" v-model="newTask.task" name="task" id="task" placeholder="e.g Do Grocery Shopping">
        </div>
        <div class="form-control">
            <label for="time">Task Day & Time</label>
            <input type="text" v-model="newTask.day" name="time" placeholder="e.g March 30 at 9:00am" id="time">
        </div>
        <div class="form-control">
            <label for="reminder">Reminder</label>
            <input type="checkbox" v-model="newTask.reminder" name="reminder" id="reminder">
        </div>
        <div class="form-control">
            <Button text="Save Task" />
        </div>
    </form>
</template>
<script>
    import Button from './Button'
    export default {
        name: 'AddTask',
        components: {
            Button,
        },
        data() {
            return {
                newTask: {
                    task: '',
                    day: '',
                    reminder: false,
                }
            }
        },
        methods: {
            createTask(e) {
                e.preventDefault();
                if(!this.newTask.task) {
                    alert("Please enter a name for your task")
                    return;
                }
                if(!this.newTask.day) {
                    alert("Please enter a time for your task")
                    return;
                }
                const newTask = {
                    id: Math.floor(Math.random()),
                    title: this.newTask.task,
                    day: this.newTask.day,
                    reminder: this.newTask.reminder
                }
                this.$emit('add-task', newTask)
                this.newTask.task = ''
                this.newTask.day = ''
                this.newTask.reminder = false;
            }
        }
    }
</script>

<style scoped>
    *{
        text-align: left;
        outline: 0;
        box-sizing: border-box;
    }
    form {
        padding: 15px 0;
        border: 1px solid #2c3e503f;
        border-width: 1px 0;
        margin-bottom: 20px;
    }
    label{
        display: block;
        font-size: 15px;
        font-weight: 400;
    }
    input{
        padding: 7px 10px;
        width: 100%;
        border: 1px solid #2c3e5031;
    }
    .form-control{
        margin: 15px 0;
        width: 100%;
        
    }
    label[for='reminder'],
    input[type='checkbox']{
        display: inline-block;
    }
    input[type='checkbox'] {
        width: auto;
        margin-left: 20%;
    }
    input::placeholder{
        opacity: 0.8;
        font-weight: 300;
        font-size: 14px;
    }
    button {
        width: 100%;
        text-align: center;
    }
</style>