<template>
    <Task @toggle-reminder="$emit('toggle-reminder', task.id)" @delete-task="$emit('delete-task', task.id)" :key="task.id" v-for="task in tasks" :task="task"/>
</template>

<script>
    import Task from './Task'
    export default {
        name: 'Tasks',
        props: {
            tasks: Array
        },
        components: {
            Task
        },
        emits: ['delete-task', 'toggle-reminder'],
    }
</script>